@use "sass:math";

////////////////////
// 
////////////////////
// 
$inner: 890px;
// 

// 
$padding-pc: 25px;
$padding-sp: 15px;
// PCPC25px


////////////////////
// 
////////////////////

$base-font-family: "",
YuGothic,
"",
"Yu Gothic",
sans-serif;
// $second-font-family: "";
// 

body {
  font-family: $base-font-family;
}

// 
$regular: 300;
$normal: 500;
$bold: 700;

body {
  font-weight: $bold;
}

////////////////////
// 
////////////////////

//
$black: #4d4d4d;
$white: #fff;
$orange: #f1a638;
$red: #ed5032;
$blue: #0082c4;


////////////////////
// 
////////////////////

//
$breakpoints: (sm: 600,
  md: 768,
  lg: 1024,
  xl: 1440,
);


//
$mediaquerys: (sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}


////////////////////
// 
////////////////////

//768px
.u-desktop {
  display: none;

  @include mq("md") {
    display: block;
  }
}

//
.u-mobile {
  @include mq("md") {
    display: none;
  }
}